.collapse {
  display: flex;
  flex-direction: column;

  width: 100%;
  height: 100%;
  :global {
    .chain-ide-anim-active {
      transition-timing-function: ease-out;
      transition-duration: 0.15s;
    }
    .chain-ide-item.chain-ide-item-active {
      flex: 1;
    }
    .chain-ide-item {
      display: flex;
      flex-direction: column;
      overflow: hidden;
      border-top-width: 1px;
      border-top-style: solid;
    }
    .chain-ide-item:first-child {
      border-top: none;
    }
    .chain-ide-item > .chain-ide-header {
      display: flex;
      align-items: center;
      box-sizing: border-box;
      height: 22px;
      padding-left: 12px;
      overflow: hidden;
      font-weight: bolder;
      line-height: 22px;
      text-transform: uppercase;
      cursor: pointer;
      user-select: none;
      &:focus {
        outline-width: 0;
        outline-style: solid;
        outline-offset: -1px;
        opacity: 1 !important;
      }
    }

    .chain-ide-item .chain-ide-header-collapsable-only {
      cursor: default;
    }
    .chain-ide-item .chain-ide-header-collapsable-only .chain-ide-header-text {
      cursor: pointer;
    }
    .chain-ide-item-disabled > .chain-ide-header {
      cursor: not-allowed;
    }
    .chain-ide-content {
      flex: 1;
      overflow: hidden;
    }
    .chain-ide-content > .chain-ide-content-box {
      height: 100%;
    }
    .chain-ide-content-inactive {
      display: none;
    }
    .chain-ide-item:last-child > .chain-ide-content {
      border-radius: 0 0 3px 3px;
    }
    .chain-ide-item.chain-ide-item-active > .chain-ide-header::before {
      font-family: FabricMDL2Icons;
      content: '';
    }
    .chain-ide-item > .chain-ide-header::before {
      display: flex;
      flex-shrink: 0;
      align-items: center;
      justify-content: center;
      width: 20px;
      margin-right: 4px;
      color: inherit;
      font-size: 12px;
      font-family: FabricMDL2Icons;
      transform-origin: center;
      content: '';
    }

    .chain-ide-item .chain-ide-extra {
      display: none;
      padding-right: 5px;
      transition: display 300ms;
    }

    .chain-ide-item.chain-ide-item-active:hover .chain-ide-extra {
      display: block;
    }

    .header {
      flex: 1 1;
      min-width: 0;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
